.main {
    
    /*  section 1   */
    .main-container {
        margin-top: 5em;
        margin-bottom: 5em;
        .container-intro{
            @extend %container-mxw2;
            margin-bottom: 8em;
            display: flex;
            align-items: center;
            .ci-titre-1 {
                margin-right: 1em;
                margin-left: 2em;
                padding-right: 1em;
                border-right: 2px solid orange;
                line-height: 3em;
                text-transform: uppercase;
            }
            .ci-description {
                    font-size: 1em;
                text-transform: uppercase;
                    line-height: 1.25em;
            }
            
        }
        .container-story {
           @extend %container-mxw3;
                display: grid;
                grid-template-columns: 1fr 5fr 3fr;
                grid-column-gap: 1.5em;
                padding-bottom: 8em;
                border-bottom: 1px solid #ccc;
                margin-bottom: 8em;
           
            .container-icone {
                border: 2px solid orange;
                border-radius: 5em;
                width: 125px;
                height: 125px;
                font-size: 2em;
                display: flex;
                align-items: center;
                justify-content: center;
            }
            .container-description {
                width: 100%;
                .cd-titre-2 {
                    text-transform: uppercase;
                    margin-bottom: 1em;
                }
                
                .ci-description-1 {
                    color: #e74c3c;
                    line-height: 1em;
                    font-size: 1em;
                    margin-bottom:1.5em;
                }
                
                .ci-description-2 {
                    font-size: 1em;
                    line-height: 1.25em;

                }
            }
            .container-picture {
                
            }
        }
        
        .container-skills {
            @extend %container-mxw3;
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-column-gap: 1.5em;
            
            .container-info-skills {
                .container-1 {
                    display: grid;
                    grid-template-columns: 1fr 3fr;
                    grid-column-gap: 1.5em;
                    
                    .container-icone {
                        border: 2px solid orange;
                        border-radius: 5em;
                        width: 125px;
                        height: 125px;
                        font-size: 2em;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                    .container-td {
                        .cis-titre-2 {
                            text-transform: uppercase;
                            margin-bottom: 1em;
                        }
                        .ci-description-1 {
                            font-size: 1em;
                            line-height: 1.5em;
                        }
                    }
                }
                
                .container-2 {
                    margin-top: 5em;
                    display: grid;
                        grid-template-rows: 1fr 1fr;
                        grid-row-gap: 1.75em;
                    
                    .progress {
                        
                        display: grid;
                        grid-template-rows: 1fr 1fr;
                        grid-row-gap: .5em;
                        
                        .progress-titre-2 {
                            .skill {
                                font-weight: bold;
                            }
                        }
                        .progress-container {
                            height: 10px;
                            background-color: #ccc;
                            .progress-bar-1 {
                                height: 100%;
                                width: 88%;
                                background-color: orange;
                                
                            }
                            .progress-bar-2 {
                                height: 100%;
                                width: 76%;
                                background-color: orange;
                                
                            }
                            .progress-bar-3 {
                                height: 100%;
                                width: 81%;
                                background-color: orange;
                                
                            }
                            .progress-bar-4 {
                                height: 100%;
                                width: 71%;
                                background-color: orange;
                                
                            }
                        }
                    }
                }
            }
        }
    }
    
    
    
    /*  section 2   */
    
    .background-image-1 {
        background: url(/images/bg1.jpg) no-repeat center top;
        background-position: 100% 40%;
        background-size: cover;
        height: 500px;
        position: relative;
        color: #fff;    
            
            .container-citation {
                position: absolute;
                @include centrer-bg-image(50%, 50%, 50%, 50%);
                @extend %container-citation;

                .quote-text {
                    font-size: 45px;
                    line-height: 60px;
                    font-weight: bold;
                    position: relative;
                    
                    &::before{
                        content: '"';
                        position: absolute;
                        top: 0;
                        left: 0;
                        font-size: 55px;
                    }
                    
                    &::after{
                        content: '"';
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 55px;
                    }
                }
                
                .quote-author {
                    text-transform: uppercase;
                    font-style: normal;
                    font-size: 24px;
                    text-align: -webkit-center;
                    
                    &::before{
                            content: '';
                            height: 5px;
                            width: 50px;
                            display: block;
                            background-color: orange;
                            margin-bottom: 2em;
                            margin-top: 2em;
                    }
                }
                
            }
        
    }
    
    .main-container {
        
        margin-top: 5em;
        margin-bottom: 5em;
        
        .container-intro {
            @extend %container-mxw2;
            margin-bottom: 8em;
            display: flex;
            align-items: center;
            
            .ci-titre-1 {
                margin-right: 1em;
                margin-left: 2em;
                padding-right: 1em;
                border-right: 2px solid orange;
                line-height: 3em;
                text-transform: uppercase;
            }
            
            .ci-description {
                font-size: 1em;
                text-transform: uppercase;
                line-height: 1.25em;
            }
            
        }
        
        .post-container {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-column-gap: 1.75em;
            @extend %container-mxw3;
            
            
            .post-left {
                .post-1 {
                    
                    position: relative;
                    .link-post {
                        
                    }
                    .post-icone {
                        position: absolute;
                        background-color: #f39c12;
                        width: 44px;
                        height: 44px;
                        color: #fff;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        @include centrer-icone(70%, 20%, 50%, 50%);
                    }
                    .container-mc {
                        display: grid;
                        grid-template-columns: 1fr 3fr;
                        grid-column-gap: 1.25em;
                        
                        .post-meta {
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            text-align: right;
                        }
                        
                        .post-content {
                            background-color: #fff;
                            padding-top: 2em;
                           
                            .link-titre{
                                color: black;
                                text-decoration: none;
                                margin-bottom: 3em;
                               .pc-titre-2 {
                                   font-size: 1em;
                                text-transform: uppercase;
                                }  
                            }
                            
                            .post-content {
                                font-size: 13px;
                                text-transform: uppercase;
                                line-height: 1.5em;
                            }
                             
                        }
                    }
                }
            }
            .post-right {
                
            }
        }
        
        
    }
    
}